<template>
    <div class="notes container">
        <h1>Notes</h1>
        
        <b-alert
            :show="dismissCountDown"
            dismissible
            :variant="message.color"
            @dismissed="dismissCountDown=0"
            @dismiss-count-down="countDownChanged"
        >

            <ul class="m-0 px-3" v-if="showErrors">
                <li class="text-left" v-for="(error, index) in errors" :key="index"> {{ error.message }} </li>
            </ul>

            <h5 class="text-left m-0">{{ message.text }}</h5>

        </b-alert>

        <form @submit.prevent="addNote" v-if="!edit">
            <input type="text" class="form-control my-2" placeholder="name" v-model="note.name" autofocus>
            <input type="text" class="form-control my-2" placeholder="description" v-model="note.description">
            <b-button variant="primary" type="submit" class="mt-2 btn-block">Add note</b-button>
        </form>

        <form @submit.prevent="editNote" v-if="edit">
            <input type="text" class="form-control my-2" placeholder="name" v-model="note.name" autofocus>
            <input type="text" class="form-control my-2" placeholder="description" v-model="note.description">
            <b-button variant="warning" type="submit" class="my-2">Update note</b-button>
            <b-button variant="danger" type="submit" class="m-2" @click="cancelEditMode">Cancel</b-button>
        </form>

    <div class="table-responsive">
        <table class="table table-striped mt-5">
            <thead>
                <tr>
                    <th scope="col">#</th>
                    <th scope="col">Name</th>
                    <th scope="col">Description</th>
                    <th scope="col">Edit</th>
                    <th scope="col">Delete</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(note, index) in notes" :key="index">
                    <th scope="row">{{ index + 1 }}</th>
                    <td>{{ note.name }}</td>
                    <td>{{ note.description }}</td>
                    <td> <b-button variant="warning" @click="editMode(note)">Edit</b-button> </td>
                    <td> <b-button variant="danger" @click="deleteNote(note._id, index)">Delete</b-button> </td>
                </tr>
            </tbody>
        </table>
    </div>

    </div>
</template>

<script>
import {mapState} from 'vuex'

export default {
    name: 'Notes',
    data() {
        return {
            note: { name: '', description: '' },
            notes: [],
            dismissSecs: 5,
            dismissCountDown: 0,
            message: {
                color: '',
                text: '',
            },
            errors: {},
            showErrors: false,
            edit: false,
            noteSelectedId: ''
        }
    },
    computed: {
        ...mapState(['token'])
    },
    methods: {
        listNotes() {
            let config = {
                headers: {
                    token: this.token
                }
            }
            this.axios.get('/notes', config)
                .then(response => this.notes = response.data)
                .catch(error => console.log(error.response))
        },
        countDownChanged(dismissCountDown) {
            this.dismissCountDown = dismissCountDown
        },
        showAlert() {
            this.dismissCountDown = this.dismissSecs
        },
        setAlert(color, message) {
            this.message.color = color
            this.message.text = message
            this.showAlert()
        },
        addNote() {
            let config = {
                headers: {
                    token: this.token
                }
            }
            this.axios.post('notes', this.note, config)
                .then(response => {
                    this.notes.push(response.data)
                    this.showErrors = false
                    this.setAlert('primary', 'Note added')
                })
                .catch(errors => {
                    console.log(errors.response)
                    this.errors = errors.response.data.error.errors
                    this.showErrors = true
                    this.setAlert('danger')
                })
        },
        deleteNote(id, index) {
            let config = {
                headers: {
                    token: this.token
                }
            }
            if (confirm("Do you really want to delete?")) {
                this.axios.delete(`notes/${id}`, config)
                .then(response => {
                    console.log(response)
                    this.setAlert('primary', 'Note deleted')
                    this.notes.splice(index, 1)
                })
                .catch(errors => {
                    this.errors = errors.response.data.error.errors
                    this.showErrors = true
                    this.setAlert('danger')
                })               
            }
        },
        editMode(note) {
            this.edit = true
            this.note.name = note.name 
            this.note.description = note.description
            this.noteSelectedId = note._id
        },
        cancelEditMode() {
            this.edit = false
            this.note = { name: '', description: '' }
        },
        editNote() {
            let config = {
                headers: {
                    token: this.token
                }
            }
            this.axios.put(`/notes/${this.noteSelectedId}`, this.note, config)
                .then(response => {
                    console.log(response.data)
                    const index = this.notes.findIndex(note => note._id === response.data._id)
                    this.notes[index].name = response.data.name 
                    this.notes[index].description = response.data.description
                    this.setAlert('primary', 'Note updated')
                })
                .catch(errors => {
                    console.log(errors.response.data.message)
                    // this.errors = errors.response.data.message 
                    this.showErrors = true 
                    this.setAlert('danger', errors.response.data.message)
                })
        }
    },
    created() {
        this.listNotes()
    }
}
</script>

